.vision-section{
    width: 100%;
    height: 100vh;
position: relative;
display: flex;
flex-direction: column;
justify-content: space-between;
 .vision-image{
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;
}

.arrow-services{
 
    text-align: center;
    
        h1{
         
            color: $VioletColor;
            font-size: 1vw;
        }
        img{
     
            width: 1%;
        }
    }
}
.vision-section .vision-container{

    display: flex; 
    text-align: center;    
    justify-content: space-around;
    z-index: 1;
    width: 80%;
    
    padding: 0 100px;
    margin: auto auto;
    
    position: relative;
   
.vision-text{
text-align: start;
width: 80vw;
margin-top: 30px;

   h1{
       color: $VioletColor;
       font-size: 1vw;
       font-weight: bolder;
       
      
   }
}
.image-right-vision{
    width: 100vw;

.vision-image-right{
        
    width:100%}
  
}
}
.vision-title{
 
    text-align: center;
    position: relative;
    padding-top: 1px;
    h1{
        color: $VioletColor;
        font-size:3vw;
        font-weight: bolder;
        line-height: normal;
    }
    h2{
        line-height: 0;
        color: $OrangeColor;
        font-size: 2.5vw;
        font-weight: bolder;
       
    }
}









