

.hero-section {
  width: 100%;
  height:100vh;
  position: relative;
  
}

.hero-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: -1;
}
.hero-content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 100px;
  .hero-text{
    h1{
      color: $SloganColor;
      font-size: 4vw;
    line-height: normal;
    }
    h2{
      color: $SloganColor;
      font-size: 3vw;
      line-height: 0px;
    }
    .hero-btn{
   
      margin-top: 12vh;
    }
  }
}
.hero-image{
  width: 30vw;
  img{
    width: 100%;
  }
}
